.Ads-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}

.Ads-container .Menu{
    max-width: 100%;
    text-align: center;
    padding: 25px 40px;
}

.Ads-container .Title-wrapper{
    justify-content: center;
}

.Ads-category{
    margin-top: 20px;
    display: flex;
    gap: 20px;
    width: 100%;
    max-width: 1200px;
}

.Ads-category .AdsItem-container{
    flex: 1;
    max-width: 100%;
}

.Ads-content {
    display: flex;
    gap: 20px;
    width: 100%;
    max-width: 1200px;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}
