.Ig-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
}

.Ig-container .Menu{
    margin-top: 30px;
    max-width: 100%;
    text-align: center;
    padding: 25px 200px;
}

.Ig-container .Title-wrapper{
    justify-content: center;
}

.Ig-wrapper {
    margin-top: 20px;
    background-color: white;
    width: 62%;
    min-height: 580px;
    max-height: 580px;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.Ig-content {
    display: flex;
    align-items: center;
    position: relative;
}

.Ig-slider {
    color: var(--dark-blue);
    position: absolute;
    top: 50%;
    transform: scale(1.5) translateY(-50%);
    background: none;
    border: none;
    cursor: pointer;
    transition: 0.5s;
    z-index: 10;
}

.Ig-slider:hover {
    color: black;
    transform: scale(1.8) translateY(-40%);
}

.Ig-slider:active {
    color: var(--dark-blue);
    transform: scale(1.5) translateY(-50%);
}

.Ig-slider.left {
    left: -60px;
}

.Ig-slider.right {
    right: -60px;
}

.Dots-container {
    position: absolute;
    bottom: 25px;
    display: flex;
    gap: 20px;
    justify-content: center;
    margin-bottom: 5px;
}

.dot {
    width: 5px;
    height: 5px;
    background: #bbb;
    border-radius: 50%;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.3s ease;
}

.dot.active {
    background: var(--dark-blue);
    transform: scale(1.3);
}
