.Shop-scroll {
    position: relative;
    width: 62%;
    max-width: 62%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Shop-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 15px;
}

.Border-line{
    width: 100%;
}

.Shop-container .Menu{
    margin-top: 30px;
    max-width: 100%;
    text-align: center;
    padding: 25px 230px;
}

.Shop-container .Title-wrapper{
    justify-content: center;
}

.Shop-wrapper {
    margin-top: 10px;
    display: flex;
    gap: 20px;
    overflow-x: hidden;
    scroll-behavior: smooth;
    padding: 10px;
}


.Scroll-button {
    position: absolute;
    top: 50%;
    transform: scale(1.5) translateY(-50%);
    background: none;
    color: white;
    border: none;
    cursor: pointer;
    transition: transform 0.2s ease, color 0.2s ease;
    z-index: 10;
}

.Scroll-button:hover {
    color: white;
    transform: scale(1.8) translateY(-40%);
}

.Scroll-button:active {
    color: rgba(255, 255, 255, 0.651);
    transform: scale(1.5) translateY(-50%);
}

.Scroll-button.left {
    left: -50px;
}

.Scroll-button.right {
    right: -50px;
}

.scroll-indicator {
    width: 5%;
    height: 2px;
    background: rgba(255, 255, 255, 0.144);
    border-radius: 5px;
    overflow: hidden;
    margin-top: 10px;
}

.scroll-progress {
    height: 100%;
    background: #ffffff;
    width: 0%;
    transition: width 0.2s ease-in-out;
}
